@import '../../../config.css'; 


.onShade {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.30;
    z-index: 1;
    -webkit-transition: background-color 0.5s;
    -o-transition: background-color 0.5s;
    transition: background-color 0.5s;
}


.button {
    position: fixed;
    right: 0%;
    bottom: 0%;
    z-index: 2000;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: var(--buttonColor);
    margin: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-shadow: 0px 0px 2px #000000;
            box-shadow: 0px 0px 2px #000000;
    -webkit-transition: border-radius 0.2s ease-in-out, bottom 0.2s ease-in-out, right 0.2s ease-in-out, height 0.2s ease-in-out, width 0.2s ease-in-out, margin 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    transition: border-radius 0.2s ease-in-out, bottom 0.2s ease-in-out, right 0.2s ease-in-out, height 0.2s ease-in-out, width 0.2s ease-in-out, margin 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    -o-transition: border-radius 0.2s ease-in-out, bottom 0.2s ease-in-out, right 0.2s ease-in-out, height 0.2s ease-in-out, width 0.2s ease-in-out, margin 0.2s ease-in-out, transform 0.2s ease-in-out;
    transition: border-radius 0.2s ease-in-out, bottom 0.2s ease-in-out, right 0.2s ease-in-out, height 0.2s ease-in-out, width 0.2s ease-in-out, margin 0.2s ease-in-out, transform 0.2s ease-in-out;
    transition: border-radius 0.2s ease-in-out, bottom 0.2s ease-in-out, right 0.2s ease-in-out, height 0.2s ease-in-out, width 0.2s ease-in-out, margin 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    /* transition: all 0.6s ease-in-out; */
}


.close {
    position: absolute;
    right: 0%;
    top: 0%;
    z-index: 2000;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color:var(--buttonColor);
    margin: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-shadow: 0px 0px 2px #000000;
            box-shadow: 0px 0px 2px #000000;
    -webkit-transition:  all 0.2s ease-in;
    -o-transition:  all 0.2s ease-in;
    transition:  all 0.2s ease-in;
}




